import React from "react";
import Search from "../reportedcontent/search";

const ReportDashboard = () => {
  return (
    <div className="w-full h-screen flex flex-col overflow-hidden">

      {/* ðŸ”¥ Sticky Topbar */}
      <div className="w-full sticky top-0 z-50 bg-white shadow-sm">
        <Search />
      </div>

      {/* ðŸ”¥ Scrollable Content Area */}
      <div className="flex-1 overflow-auto p-4">
        <h2 className="text-lg font-semibold">Reported Content List</h2>

        {/* Dummy Content to test scrolling */}
        <div className="mt-4 space-y-4">
          {[...Array(50)].map((_, i) => (
            <div
              key={i}
              className="p-4 border rounded-xl bg-gray-100"
            >
              Item {i + 1}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ReportDashboard;
